import { Suspense } from "react"
import { Canvas } from "@react-three/fiber"
import { useLoader } from 'react-three-fiber';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { Environment, OrbitControls, PerspectiveCamera } from "@react-three/drei";

import './ThreeSection.css';
import { Clock, AnimationMixer } from "three";

let mixer : AnimationMixer;
const clock = new Clock();

const Model = () => {
    const gltf = useLoader(GLTFLoader, "./wedding2.gltf");
    const model = gltf.scene;
    mixer = new AnimationMixer(model);
    for (let i = 0; i < gltf.animations.length; i++) {
        mixer.clipAction( gltf.animations[i] ).play();
    } 
    animate();
    return (
        <>
            <primitive object={gltf.scene} scale={1} />
        </>
    );
};

function animate() {

    requestAnimationFrame( animate );

    const delta = clock.getDelta();

    mixer.update( delta );
}

export default function PlayMobiles() {
    return (
        <div className="three-section">
            <Canvas shadows>
                <ambientLight />
                <directionalLight position={[-5, 5, 5]} castShadow shadow-mapSize={1024} />
                <group position={[0, -5, 0]}>
                    <Suspense fallback={null}>
                        <PerspectiveCamera position={[0, 0, -8]} onUpdate={(c) => c.updateProjectionMatrix()}>
                            <Model />
                            {/* <OrbitControls /> */}
                            <Environment preset="sunset" background />
                        </PerspectiveCamera>
                    </Suspense>
                </group>
            </Canvas>
        </div>

    )
}